body{
  background: #f7f7f7;
}

.flyout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: space-between;
}

// Custom colors
.bg-dark {
  background-color: $dark !important;
  &.darken-1 {
    background-color: darken($dark, 5%) !important;
  }
  &.darken-2 {
    background-color: darken($dark, 10%) !important;
  }
  &.darken-3 {
    background-color: darken($dark, 15%) !important;
  }
  &.lighten-1 {
    background-color: lighten($dark, 10%) !important;
  }
  &.lighten-2 {
    background-color: lighten($dark, 20%) !important;
  }
  &.lighten-3 {
    background-color: lighten($dark, 30%) !important;
  }
}
.bg-red {
  background-color: $red !important;
  &.darken-1 {
    background-color: darken($red, 10%) !important;
  }
  &.darken-2 {
    background-color: darken($red, 20%) !important;
  }
  &.darken-3 {
    background-color: darken($red, 30%) !important;
  }
  &.lighten-1 {
    background-color: lighten($red, 10%) !important;
  }
  &.lighten-2 {
    background-color: lighten($red, 20%) !important;
  }
  &.lighten-3 {
    background-color: lighten($red, 30%) !important;
  }
}
.bg-green {
  background-color: $green !important;
  &.darken-1 {
    background-color: darken($green, 10%) !important;
  }
  &.darken-2 {
    background-color: darken($green, 20%) !important;
  }
  &.darken-3 {
    background-color: darken($green, 30%) !important;
  }
  &.lighten-1 {
    background-color: lighten($green, 10%) !important;
  }
  &.lighten-2 {
    background-color: lighten($green, 20%) !important;
  }
  &.lighten-3 {
    background-color: lighten($green, 30%) !important;
  }
}

// Custom text
.red-text {
  color: $red !important;
}
.pink-text {
  color: $pink !important;
}

// Buttons
.btn {
  box-shadow: none !important;
  border-radius: 0px;
  i {
    margin-right: 0.3rem;
  }
  &:hover {
    box-shadow: none;
  }
  &:active {
    box-shadow: none;
  }
  &:focus {
    box-shadow: none;
  }
  &.btn-secondary {
    background-color: #cda4f4 !important;
    &:hover {
      background-color: darken(#cda4f4, 5%) !important;
    }
    &:focus,
    &:active {
      background-color: darken(#cda4f4, 7%) !important;
    }
  }
  &.btn-green {
    background-color: #81c784 !important;
    &:hover {
      background-color: darken(#81c784, 7%) !important;
    }
    &:focus,
    &:active {
      background-color: darken(#81c784, 10%) !important;
    }
  }
  &.btn-outline-green {
    border: 2px solid #81c784 !important;
    &:hover {
      background-color: lighten(#81c784, 30%) !important;
      border: 2px solid darken(#81c784, 7%) !important;
    }
    &:focus,
    &:active {
      border: 2px solid darken(#81c784, 10%) !important;
    }
  }
}

// Footer
footer {
  background-color: #cda4f4 !important;
}

// Cards
.card {
  box-shadow: none;
  border-radius: 0px;
}

// Generic
.clickable {
  cursor: pointer !important;
}

// Scrollbar
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f5;
}
::-webkit-scrollbar {
  width: 12px;
  background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #81c784;
}

